#plain {
    width: 600px;
    height: 600px;
    border: solid 2px #cccccc;
    margin: 10px auto 0;
    position: relative;
}


#ball {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: green;
    position: absolute;
}

#text {
    border: solid 1px black;
    position: relative;
    left: 50px;
    bottom: 50px;
}
